<template>
  <div class="profile-page">
    <h1>내 정보</h1>
    <div class="info-row">
      <div class="info-label">사용자 이름:</div>
      <div class="info-text">{{ userName }}</div>
    </div>
    <div class="info-row">
      <div class="info-label">관심 카테고리:</div>
      <div class="info-text">
        <select v-model="userState" class="category-select">
          <option value="" disabled hidden>카테고리를 선택하세요</option>
          <option v-for="(State, index) in states" :key="index" :value="State">{{ State }}</option>
        </select>
      </div>
    </div>
    <button @click="logout" class="logout-btn">로그아웃</button>
    <router-link to="/main" class="back-link">메인페이지로 돌아가기</router-link>
  </div>
</template>

<script>
import { getAuth, onAuthStateChanged, signOut } from 'firebase/auth';

export default {
  name: 'ProfilePage',
  data() {
    return {
      firebaseUser: null, // Firebase 사용자 정보를 저장할 객체
      userName: '냉장고',
      states: ['카테고리','당뇨', '다이어트', '비건'],
      userState: '',
    };
  },
  mounted() {
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
      if (user) {
        this.firebaseUser = user;
        this.userName = user.displayName;
      } else {
        this.firebaseUser = null;
        this.userName = '냉장고';
      }
    });
    
  },
  methods: {
    logout() {
  const auth = getAuth();
  // confirm 팝업을 사용하여 로그아웃을 확인
  if (confirm('로그아웃 하시겠습니까?')) {
    signOut(auth)
      .then(() => {
        console.log('로그아웃 성공');
        alert('로그아웃 되었습니다.'); // 로그아웃 성공 메시지
        this.$router.push('/');
      })
      .catch((error) => {
        console.error('로그아웃 실패:', error);
        alert('로그아웃 실패: ' + error.message); // 로그아웃 실패 메시지
      });
  } else {
    // 사용자가 로그아웃을 취소할 경우
    console.log('로그아웃 취소');
  }
    },
  },
};
</script>


