  font-size: 18px;
  color: #666;
}

/* 텍스트 박스 스타일 */
.info-text {
  flex: 2;
  font-size: 18px;
  letter-spacing: 1px; /* 글자 간격 조절 */
}

/* 선택 박스 스타일 */
.category-select {
  padding: 8px;
  font-size: 18px;
  width: 200px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

/* 로그아웃 버튼 스타일 */
.logout-btn {
  display: block;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  text-align: center;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

