  <script>
import axios from 'axios';

export default {
  name: 'MainPage',
  data() {
    return {
      showAddPopup: false,
      showEditPopup: false,
      selectedIngredient: null,
      newIngredient: {
        u_ingredientName: '',
        u_ingredientDateType: 'true',
        u_ingredientDate: '',
        u_ingredientAmount: 1,
        u_ingredientUnit: '개',
        u_ingredientStorage: '냉장',
        u_ingredientId: null
      },
      fridgeIngredients: [],
      freezerIngredients: [],
      roomIngredients: []
    };
  },
  methods: {
    showAddIngredientPopup() {
      this.showAddPopup = true;
      this.showEditPopup = false;
      this.newIngredient = {
        u_ingredientName: '',
        u_ingredientDateType: 'true',
        u_ingredientDate: '',
        u_ingredientAmount: 1,
        u_ingredientUnit: '개',
        u_ingredientStorage: '냉장',
        u_ingredientId: null
      };
    },
    showEditIngredientPopup(u_ingredient) {
      this.showEditPopup = true;
      this.showAddPopup = false;
      this.selectedIngredient = u_ingredient;
      this.newIngredient = { ...u_ingredient };
    },
    closePopup() {
      this.showAddPopup = false;
      this.showEditPopup = false;
    },
    addIngredient() {
  this.newIngredient.u_ingredientId = Date.now().toString();
  this.newIngredient.u_ingredientDateType = 'PURCHASE_DATE';
  this.newIngredient.u_ingredientDate = new Date().toISOString().split('T')[0]; // 현재 날짜를 'YYYY-MM-DD' 형식으로 변환

  this.newIngredient.u_ingredientAmount = parseFloat(this.newIngredient.u_ingredientAmount); // 숫자 형식으로 변환

  axios.post('http://localhost:8081/api/main', this.newIngredient)
    .then(response => {
      console.log("Ingredient saved successfully:", response.data);
      this.updateIngredientList();
      this.closePopup();
    })
    .catch(error => {
      console.error("Error saving ingredient:", error);
    });
},

    editIngredient() {
      axios.put(`http://localhost:8081/api/main/${this.selectedIngredient.u_ingredientId}`, this.newIngredient)
        .then(response => {
          console.log("Ingredient updated successfully:", response.data);
          this.moveIngredientBetweenSections(this.selectedIngredient, this.newIngredient.u_ingredientStorage);
          this.closePopup();
        })
        .catch(error => {
          console.error("Error updating ingredient:", error);
        });
    },
    moveIngredientBetweenSections(u_ingredient, destinationMethod) {
      const id = u_ingredient.u_ingredientId;
      this.removeIngredient(id, u_ingredient.u_ingredientStorage);

      const sectionMap = {
        냉장: this.fridgeIngredients,
        냉동: this.freezerIngredients,
        실온: this.roomIngredients
      };

      const targetSection = sectionMap[destinationMethod];
      if (targetSection) {
        targetSection.push({ ...u_ingredient, u_ingredientStorage: destinationMethod });
      }
    },
    removeIngredient(id, u_ingredientStorage) {
      switch (u_ingredientStorage) {
        case '냉장':
          this.fridgeIngredients = this.fridgeIngredients.filter(u_ingredient => u_ingredient.u_ingredientId !== id);
          break;
        case '냉동':
          this.freezerIngredients = this.freezerIngredients.filter(u_ingredient => u_ingredient.u_ingredientId !== id);
          break;
        case '실온':
          this.roomIngredients = this.roomIngredients.filter(u_ingredient => u_ingredient.u_ingredientId !== id);
          break;
        default:
          console.error('Invalid u_ingredientStorage:', u_ingredientStorage);
          return;
      }
    }
  }
}
</script>

  

  
